  --clr-black: #000;
  --clr-deep-black: #0d0d0d;
  --clr-contrast-black: #121212;
  --clr-grey: #6c6c6c;
  --clr-soft-grey: #4d4d4d;
  --clr-deep-grey: #1b1b1b;
