  --line-height-400: 1.2;
  --fs-h1: 6.25rem;
  --fs-h2: 5rem;
  --fs-h3: 2rem;
  --fs-h4: var(--fs-400);
  --fs-body-uppercase: .875rem;
  --fs-body-regular: .875rem;
  --fs-body-small: .75rem;
  --fs-helper-text: .5rem;
  --letter-spacing-h1: -.25rem;
  --letter-spacing-h2: -.2rem;
  --letter-spacing-h3: var(--letter-spacing-500);
  --letter-spacing-h3: var(--letter-spacing-500);
  --letter-spacing-h4: var(--letter-spacing-500);
  --line-height-h1: 1.05;
  --line-height-h2: var(--line-height-h1);
  --line-height-h3: 1.1;
  --line-height-h4: 1.3;
